import React,{Component} from 'react'
import axios from 'axios'
import {

  Link
} from 'react-router-dom'

import NewsCard from './NewsCard'



export default class NewsDetails extends Component{

	constructor(props) {
	  super(props);
	
	  this.state = {newsdata:'s'};
	  this.loadPageInSame=this.loadPageInSame.bind(this)
	}

	componentDidMount(){

		axios.get('https://gist.githubusercontent.com/nikethsingh/32b226fdcc15ce09e8083b15d5698d83/raw/e1784b0bb993f332abc53f9862e36ecefa78e05c/news.json')
  .then( (response)=> {
    console.log(response.data.articles);
    console.log(response.data.articles[0].author)
    this.setState({
    	newsdata:response.data.articles,
    	
    })
  })
  .catch(function (error) {
    console.log(error);
  });
	}

		loadPageInSame(index){
		console.log(index)

		
	this.props.history.push(`/news/${index}`);
}


	render(){


		

		if(this.state.newsdata==="s")


		 {
		 	return(
  				<div className="loader"> </div>

		 	)
		 }
		 else

		{
			var index=this.props.match.params.token
			var news=this.state.newsdata[index]

var arr = this.state.newsdata.filter(item => item !== news)

console.log(arr)
			return(
				<div className="container-fluid">

				<div className="row" 
							style={{marginBottom:'4rem',marginTop :'.5rem'}}>
							  <div className="col-lg-2 col-md-2 col-sm-2">Logo</div>
							  <div className="col-lg-8 col-md-8 col-sm-8">
							  	<input type="search" />
		
							  </div>
							  <div className="col-lg-2 col-md-2 col-sm-2">
							  	<button className="button">
							  	{/*<span className="fa fa-user"></span>*/}
							  <Link to="/dashboard">	Admin</Link>

							  </button>
							  </div>
		
		
					</div>

					<div className="row">
						<div className=" col-xs-12 col-sm-12 col-md-8 col-lg-8 " style={{boxShadow:'10px 0px 5px -2px lightgrey'}}>

							<div className="col-lg-12 col-md-12 col-sm-12" style={{marginBottom:'6rem'}}>
								<h3 style={{marginTop:'4rem'}}>{news.title}</h3>
							</div>

							<div className="col-lg-12 col-md-12 col-sm-12"  style={{marginBottom:'6rem'}}>
								<img className="news-details-image" src={news.urlToImage} alt="pics"/>
							</div>

							<div className="col-lg-12 col-md-12 col-sm-12" style={{marginBottom:'6rem'}}>
								<p className="news-details-desc">{news.description}</p>
							</div>


						</div>


						<div className="col-lg-4 col-md-4 col-sm-12 col-xs-12 sideBarNews">

						{arr.map((data)=>

<div onClick={()=>this.loadPageInSame(data.index)}>
 								<NewsCard 
 									  className="wrapper-side"
								      newsPic={data.urlToImage} 
								      newsTitle={data.title}
									/>

</div>
							)}
							


						</div>
					</div>
				
				</div>

				)
		}
	}
}


